import { Inject, Injectable, InjectionToken, Injector, Optional } from '@angular/core';
import { ServiceDataHttpConfig } from './data-http-config';
import { fromExamples, MockData, MockExample, MocksItem, MOCKS_EXAMPLES } from './mocks';
import { joinUrl } from './url';

const DEFAULT_CONFIG_TOKEN = new InjectionToken<Array<MockData>>(`@backbase/foundation default config token`);

@Injectable({ providedIn: 'root' })
export class DataModulesManager {
  private _mocks: Array<MocksItem> = [];

  private modulesConfig = new Map<
    InjectionToken<ServiceDataHttpConfig>,
    Partial<{
      examples: Array<MockExample>;
      config: ServiceDataHttpConfig;
    }>
  >();

  constructor(
    private injector: Injector,
    @Optional()
    @Inject(MOCKS_EXAMPLES)
    mockData?: Array<MockData>,
  ) {
    this.addMockExamples(mockData || []);
  }

  setModuleConfig(token: InjectionToken<ServiceDataHttpConfig>, config: ServiceDataHttpConfig) {
    if (this.modulesConfig.has(token)) {
      const current = this.modulesConfig.get(token);
      this.modulesConfig.set(token, { ...current, config });
    } else {
      this.modulesConfig.set(token, {
        config,
      });
    }
    this.updateMocks();
  }

  getMocks(): ReadonlyArray<MocksItem> {
    return this._mocks;
  }

  private addMockExamples(mockData: Array<MockData>) {
    for (const { token, examples } of mockData) {
      const configToken = token || DEFAULT_CONFIG_TOKEN;
      const config = this.injector.get<ServiceDataHttpConfig | null>(configToken, null) ?? undefined;

      const allExamples =
        configToken === DEFAULT_CONFIG_TOKEN
          ? examples.concat(this.modulesConfig.get(configToken)?.examples ?? [])
          : examples;

      this.modulesConfig.set(configToken, {
        examples: allExamples,
        config,
      });
    }

    this.updateMocks();
  }

  private updateMocks() {
    const mockExamples: Array<MockExample> = [];
    for (const { config, examples } of this.modulesConfig.values()) {
      mockExamples.push(...(examples || []).map(example => this.createURL(example, config)));
    }
    this._mocks = fromExamples(mockExamples);
  }

  private createURL(example: MockExample, serviceConfig?: ServiceDataHttpConfig): MockExample {
    return serviceConfig
      ? {
          ...example,
          ...{ urlPattern: joinUrl(serviceConfig.apiRoot, serviceConfig.servicePath, example.urlPattern) },
        }
      : example;
  }
}
