import {
  HttpErrorResponse,
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpResponse,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { APP_BOOTSTRAP_LISTENER, Injectable, InjectionToken, isDevMode, Provider } from '@angular/core';
import { Observable, of as observableOf, throwError } from 'rxjs';
import { delay, dematerialize, materialize, tap } from 'rxjs/operators';
import { ServiceDataHttpConfig } from './data-http-config';
import { DataModulesManager } from './data-module-manager';
import { isSuccessStatus, MockExample, MocksItem, MOCKS_EXAMPLES } from './mocks';

const DEFAULT_OPTIONS = {
  responseDelay: 300, // ms
  enableMocksStorageKey: 'enableMocks',
};

@Injectable({
  providedIn: 'root',
})
export class MocksInterceptor implements HttpInterceptor {
  private readonly options: MocksInterceptorConfiguration = DEFAULT_OPTIONS;

  private urlMatchesPattern(url: string, pattern: string) {
    const regexp = new RegExp(
      pattern
        .replace(/[.*+?^${}()|[\]\\]/g, '\\$&') // escape regex characters in URL pattern
        .replace(/\\{[^\}]*\}/g, '[^/]*') // convert template {placeholders} to regex
        .concat('$'), // anchor to end of url
    );
    // fixes bug with older versions of data modules, where they could have ended up
    // with double slashes in the request URL, depending on the environment
    // by replacing multiple slashes with a single slash (except after ":" - e.g. in "http://")
    const normalUrl = url.replace(/([^:]\/)\/+/g, '$1');
    return regexp.test(normalUrl);
  }

  private findRequestMock<R>(request: HttpRequest<R>): MocksItem | undefined {
    const urlAndMethodMatch = (item: MocksItem) => {
      return request.method === item.method && this.urlMatchesPattern(request.url, item.urlPattern);
    };

    return this.dataModuleManager.getMocks().find(urlAndMethodMatch);
  }

  private toResponseObject<T>(mockItem: MocksItem): Observable<HttpEvent<T>> {
    const response = mockItem.responses.find(resp => resp.status === mockItem.statusToReturn);
    if (!response) {
      return throwError(
        new HttpErrorResponse({
          status: 500,
          statusText: 'No Mock Defined',
        }),
      );
    }

    if (isSuccessStatus(response.status)) {
      return observableOf(new HttpResponse(response));
    } else {
      return throwError(new HttpErrorResponse(response));
    }
  }

  /*
   * this class is constructed in createMocksInterceptor 
   function so make sure to updated it too if you have changes in the dependancy list
   */
  constructor(private readonly dataModuleManager: DataModulesManager) {}

  private getMockItemFromStorage(urlPattern: string): Partial<MocksItem> {
    try {
      return JSON.parse(localStorage.getItem(urlPattern) || '{}');
    } catch {
      console.warn(`[MOCK] Invalid json for the "${urlPattern}" local storage item`);
      return {};
    }
  }

  private processRequest<R, T>(request: HttpRequest<R>, next: HttpHandler): Observable<HttpEvent<T>> {
    const mock = this.findRequestMock(request);
    if (mock) {
      return this.toResponseObject<T>({ ...mock, ...this.getMockItemFromStorage(mock.urlPattern) });
    }
    return next.handle(request);
  }

  intercept<R, T>(request: HttpRequest<R>, next: HttpHandler): Observable<HttpEvent<T>> {
    if (localStorage.getItem(this.options.enableMocksStorageKey) !== 'true') {
      return next.handle(request);
    }

    return this.processRequest<R, T>(request, next).pipe(
      materialize(),
      delay(this.options.responseDelay),
      dematerialize(),
      tap(response => {
        // eslint-disable-next-line  no-console
        console.log('[MOCK]', request, response);
      }),
    );
  }
}

export interface MocksInterceptorConfiguration {
  responseDelay: number;
  enableMocksStorageKey: string;
}

/**
 * Create a HTTP_INTERCEPTOR to return mock responses for data modules.
 */
export function createMocksInterceptor(): Provider {
  return [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: MocksInterceptor,
      deps: [DataModulesManager],
      multi: true,
    },
    {
      provide: APP_BOOTSTRAP_LISTENER,
      useValue: bootstrapMocksInterceptor,
      multi: true,
    },
  ];
}

export function bootstrapMocksInterceptor() {
  if (!isDevMode()) {
    console.warn('Mocks should not be enabled in a production environment.');
  }
}

/**
 * @internal
 * @deprecated
 */
export function createMockInterceptor(examples: Array<MockExample>): Provider {
  return createMocks(examples);
}

/**
 * Create mocks
 *
 * @param examples
 * @param serviceConfigToken
 */
export function createMocks(
  examples: Array<MockExample>,
  serviceConfigToken?: InjectionToken<ServiceDataHttpConfig>,
): Provider {
  return {
    provide: MOCKS_EXAMPLES,
    useValue: { examples, token: serviceConfigToken },
    multi: true,
  };
}
