import { HttpParams } from '@angular/common/http';

export interface PageParams {
  from: number;
  size: number;
}
namespace PageParams {
  export function equal(a: PageParams, b: PageParams): boolean {
    return a.size === b.size && a.from === b.from;
  }
  export function toHttpParams(params: PageParams): HttpParams {
    return new HttpParams({
      fromObject: {
        from: Math.floor(params.from).toString(),
        size: Math.floor(params.size).toString(),
      },
    });
  }
}
export interface CursorParams {
  cursor: string;
  size?: number;
}
namespace CursorParams {
  export function equal(a: CursorParams, b: CursorParams): boolean {
    return a.size === b.size && a.cursor === b.cursor;
  }
  export function toHttpParams(params: CursorParams): HttpParams {
    return new HttpParams({
      fromObject: {
        from: params.cursor,
        ...(params.size ? { size: Math.floor(params.size).toString() } : {}),
      },
    });
  }
}
export type PaginationParams = PageParams | CursorParams;
export namespace PaginationParams {
  export function equal(a: PaginationParams, b: PaginationParams): boolean {
    const cursorsEqual = isCursorParams(a) && isCursorParams(b) && CursorParams.equal(a, b);
    const pagesEqual = isPageParams(a) && isPageParams(b) && PageParams.equal(a, b);
    return cursorsEqual || pagesEqual;
  }

  export function isPageParams(params: PaginationParams): params is PageParams {
    return (params as PageParams).from !== undefined;
  }
  export function isCursorParams(params: PaginationParams): params is CursorParams {
    return (params as CursorParams).cursor !== undefined;
  }

  export function toHttpParams(params: PaginationParams): HttpParams {
    return isPageParams(params) ? PageParams.toHttpParams(params) : CursorParams.toHttpParams(params);
  }
}
