import { InjectionToken } from '@angular/core';

/**
 * This token can be used to inject default DataHttpConfig
 */
export const DATA_HTTP_CONFIG = new InjectionToken<DataHttpConfig>('DataHttpConfig');
export const DATA_HTTP_CUSTOM_CONFIG = new InjectionToken<DataHttpConfig>('DataHttpCustomConfig');

export interface DataHttpConfig {
  readonly apiRoot: string;
  readonly headers: { [key: string]: string };
}

export interface ServiceDataHttpConfig extends DataHttpConfig {
  readonly servicePath: string;
}

/**
 * Returns merged service data http configuration
 * @param globalConfig Default service configuration object
 * @param serviceConfig Service configuration object to be merge on default
 */
export function createServiceDataHttpConfig(
  globalConfig: ServiceDataHttpConfig,
  serviceConfig: Partial<ServiceDataHttpConfig>,
): ServiceDataHttpConfig {
  return {
    ...globalConfig,
    ...serviceConfig,
  };
}
