import { HTTP_INTERCEPTORS, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable, Provider } from '@angular/core';
import { Observable } from 'rxjs';

import { RequestCacheService } from './request-cache.service';

@Injectable()
export class RequestCacheInterceptor implements HttpInterceptor {
  constructor(private readonly cache: RequestCacheService) {}

  intercept(req: HttpRequest<any>, next: HttpHandler) {
    if (!this.isCachable(req)) {
      return next.handle(req);
    }

    return this.cachedResponse(req) || this.refreshResponse(req, next);
  }

  private isCachable(req: HttpRequest<any>): boolean {
    return req.method === 'GET';
  }

  private cachedResponse(req: HttpRequest<any>): Observable<HttpEvent<any>> | undefined {
    return this.cache.get(req);
  }

  private refreshResponse(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const response = next.handle(req);
    this.cache.set(req, response);
    return response;
  }
}

export const requestCacheInterceptorProvider: Provider = [
  RequestCacheService,
  {
    provide: HTTP_INTERCEPTORS,
    useClass: RequestCacheInterceptor,
    multi: true,
  },
];
