import { InjectionToken } from '@angular/core';
import { ServiceDataHttpConfig } from './data-http-config';

const DEFAULT_RETURN_STATUS = 200;

export interface MockData {
  examples: Array<MockExample>;
  token?: InjectionToken<ServiceDataHttpConfig>;
}

export interface MockDataConfig {
  examples: Array<MockExample>;
  config?: ServiceDataHttpConfig;
}

/**
 * Configuration of the mock responses based on matching a request.
 */
export interface MockExample {
  urlPattern: string;
  method: string;
  responses: Array<MocksItemResponse<any>>;
}

/**
 * @internal
 */
export interface MocksItem extends MockExample {
  statusToReturn: number;
}

export namespace MocksItem {
  const placeholderPattern = /\{[^\}]*\}/g;

  function placeholdersCount(item: MocksItem): number {
    return (item.urlPattern.match(placeholderPattern) || []).length;
  }

  function placeholderIndex(item: MocksItem): number {
    const regex = new RegExp(placeholderPattern);
    const found = regex.exec(item.urlPattern);
    return found == null ? item.urlPattern.length : found.index;
  }

  function comparePlaceHolderCount(a: MocksItem, b: MocksItem): number {
    return placeholdersCount(a) - placeholdersCount(b);
  }

  function comparePlaceholderIndex(a: MocksItem, b: MocksItem): number {
    return placeholderIndex(b) - placeholderIndex(a);
  }

  export function compareSpecificity(a: MocksItem, b: MocksItem): number {
    return comparePlaceHolderCount(a, b) || comparePlaceholderIndex(a, b);
  }
}

export interface MocksItemResponse<T> {
  status: number;
  body?: T;
}

export function fromExamples(examples: Array<MockExample>): Array<MocksItem> {
  return examples.map(exampleItemToMockItem).sort(MocksItem.compareSpecificity);
}

function exampleItemToMockItem(example: MockExample): MocksItem {
  let defaultResponseItem: MocksItemResponse<any> | undefined;
  if (example.responses && example.responses.length) {
    defaultResponseItem =
      example.responses.find((response: MocksItemResponse<any>) => isSuccessStatus(response.status)) ||
      example.responses[0];
  }
  return {
    ...{ statusToReturn: defaultResponseItem ? defaultResponseItem.status : DEFAULT_RETURN_STATUS },
    ...example,
  };
}

export function isSuccessStatus(status: number): boolean {
  return status >= 200 && status <= 299;
}

export const MOCKS_EXAMPLES = new InjectionToken<Array<MockData>>(`@backbase/foundation Mocks Examples`);
