import { HttpEvent, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

// JavaScript equality sucks.. We have to use strings for keys.
type RequestCacheKey = string;

interface RequestCacheValue<T> {
  expireAt: number;
  value: Observable<HttpEvent<T>>;
}

const second = 1000;

@Injectable()
export class RequestCacheService {
  private readonly ttl = 1 * second;
  private readonly results = new Map<RequestCacheKey, RequestCacheValue<any>>();

  get(req: HttpRequest<any>): Observable<HttpEvent<any>> | undefined {
    this.garbageCollect();

    const cached = this.results.get(this.key(req));
    if (cached === undefined) {
      return undefined;
    }

    return cached.value;
  }

  set(request: HttpRequest<any>, response: Observable<HttpEvent<any>>): void {
    this.results.set(this.key(request), this.value(response));
  }

  private key(request: HttpRequest<any>): RequestCacheKey {
    return JSON.stringify({
      url: request.urlWithParams,
    });
  }

  private value<T>(response: Observable<HttpEvent<T>>): RequestCacheValue<T> {
    return {
      expireAt: this.expiryTime(),
      value: response,
    };
  }

  private expiryTime(): number {
    return Date.now() + this.ttl;
  }

  private isExpired(value: RequestCacheValue<any>): boolean {
    return Date.now() > value.expireAt;
  }

  private garbageCollect() {
    this.results.forEach((value, key) => {
      if (this.isExpired(value)) {
        this.results.delete(key);
      }
    });
  }
}
