import { HttpParameterCodec, HttpParams } from '@angular/common/http';
import { InjectionToken } from '@angular/core';
import { AvailableFeature, FeaturesService } from '@backbase/foundation-ang/future';

/**
 * Object used to hold Http Parameters
 */
export interface HttpParametersObject {
  /**
   * Each key is the name of the http param. Each value is the http param value.
   */
  [k: string]: string | Array<string> | undefined | number;
}

export interface NormalizedHttpParameters {
  [k: string]: string | Array<string>;
}

/**
 * Normalize a map of `HttpParametersObject` and return a valid `HttpParam`.
 *
 * This does 3 things:
 *  - Filter out `undefined` values.
 *  - Convert all `number` values into `strings`.
 *  - Encode values using a custom encoder (fixes this [known bug](https://github.com/angular/angular/issues/18261)).
 */
export type HttpParamsFactory = (params: HttpParametersObject) => HttpParams;

export const normalizeHttpParameter = (
  accum: NormalizedHttpParameters,
  [key, value]: [string, string | Array<string> | undefined | number],
) => {
  if (value === undefined) {
    return accum;
  }
  if (typeof value === 'number') {
    return { ...accum, [key]: String(value) };
  }
  return { ...accum, [key]: value };
};

export const EXTRA_ENCODE_URI_PARAMS: AvailableFeature = {
  name: 'EXTRA_ENCODE_URI_PARAMS',
  description:
    'Correctly encode certain symbols in data module http params (eg encode "+" to "%2B) to fix a known angular bug (#18261)',
};

/**
 * Filter undefined params, and convert numbers to strings.
 */
export const normalizeHttpParameters = (params: HttpParametersObject): NormalizedHttpParameters =>
  Object.entries(params).reduce(normalizeHttpParameter, {});

export class CustomEncoder implements HttpParameterCodec {
  encodeKey(key: string): string {
    return encodeURIComponent(key);
  }

  encodeValue(value: string): string {
    return encodeURIComponent(value);
  }

  decodeKey(key: string): string {
    return decodeURIComponent(key);
  }

  decodeValue(value: string): string {
    return decodeURIComponent(value);
  }
}

/**
 * Injection token for the `HttpParamsFactory` utility function.
 *
 * @usageNotes
 *
 * ### Example
 *
 * ```
 * export class MyDataService {
 *   constructor(private readonly http: HttpClient,
 *   @Inject(HTTP_PARAMS_FACTORY)
 *   private readonly toHttpParams: HttpParamsFactory,
 *   ){}
 *
 *   makeRequest(params: HttpParameters): Observable<HttpResponse<any>> {
 *     return this.http.request('get', '/api', {
 *       params: this.toHttpParams(params),
 *     });
 *   }
 * }
 * ```
 */
export const HTTP_PARAMS_FACTORY = new InjectionToken<HttpParamsFactory>('Http Param Factory');

export const httpParamsFactoryFactory = (featuresService: FeaturesService) => {
  // We can't type options correctly, because angular is not exporting HttpParamsOptions
  // https://github.com/angular/angular/issues/20276
  const httpParamsOptions = featuresService.isEnabled(EXTRA_ENCODE_URI_PARAMS) ? { encoder: new CustomEncoder() } : {};
  return (fromObject: HttpParametersObject): HttpParams =>
    new HttpParams({ ...httpParamsOptions, fromObject: normalizeHttpParameters(fromObject) });
};
